import { Block, CetusTooltip } from '@cetus/design'
import { Center, HStack, Image, Skeleton, Text, VStack } from '@chakra-ui/react'
import { ProfileTabsProps } from './type'

function PCProfileTabs({ tabList, activeTab, tabData, onClickTab }: ProfileTabsProps) {
  return (
    <HStack w="100%" mt="10px">
      {tabList.map((item: any) => (
        <Block
          key={item.value}
          w="25%"
          p="24px 16px"
          h="94px"
          bgImage={activeTab === item.value ? 'url(/images/tab_active.png)' : 'none'}
          bgSize="100% 100%"
          borderRadius="16px"
          display="flex"
          justifyContent="space-between"
          alignItems="center"
          cursor="pointer"
          bg="block_color_opacity.50"
          backdropFilter="blur(20px)"
          onClick={() => onClickTab(item.value)}
          _hover={{
            bgImage: 'url(/images/tab_active.png)',
            bgSize: '100% 100%',
            img: {
              opacity: '0.8'
            }
          }}
        >
          <VStack align="flex-start" gap="12px">
            <Text fontSize="14px" color="text_caption">
              {item.title}
            </Text>
            <HStack h="22px">
              <CetusTooltip
                placement="top"
                tooltip={
                  <Text fontSize="12px" lineHeight="20px" color="text_caption">
                    {item.tooltip}
                  </Text>
                }
                showTooltip={item.showTooltip}
              >
                <Center as="button">
                  <Skeleton isLoaded={!tabData[item.value]?.isLoading}>
                    <Text fontSize="22px" fontWeight="500" color="text_caption">
                      {tabData[item.value]?.totalValue}
                    </Text>
                  </Skeleton>
                </Center>
              </CetusTooltip>
            </HStack>
          </VStack>
          <Image w="48px" h="48px" src={item.activeImgUrl} opacity={activeTab === item.value ? '0.8' : '0.4'} />
        </Block>
      ))}
    </HStack>
  )
}

export default PCProfileTabs
